import './lib/jquery.fancybox.js';

$(() => {
	svg4everybody();
});

//  

$('.slide-down a').on('click', function (e) {
e.preventDefault();
var target = this.hash,
$target = $(target);
$('html, body').stop().animate({
'scrollTop': $target.offset().top
}, 800, 'swing', function () {});
});

//   
$('.hamburger-btn').click(function(){
		$(this).toggleClass('is-active');
		$(this).parent().toggleClass('is-active');
	});

// Filter begin

// Range slider begin
var rangeSlider = function(){
  var slider = $('.filter-price__range'),
      range = $('.filter-price__range .input-range'),
      value = $('.filter-price__range-value .num');
    
  slider.each(function(){

    value.each(function(){
      var value = $(this).parent().prev().attr('value');
      $(this).text(value);
    });

    range.on('input', function(){
      $(this).next().children('.num').text(this.value);
    });
  });
};

rangeSlider();
// Range slider end

$('.filter-dropdown__title, .filter-price__title').click(function () {
	$(this).toggleClass('is-active');
	$(this).next().slideToggle('fast');
});

$('.card__filter-btn').click(function () {
	$('.left-filter').toggleClass('is-active');
});

// Filter end

$('.card-tabs .btn').click(function () {
	var btnNum = $(this).index();
	$('.card-tabs .btn').removeClass('is-active');
	$(this).addClass('is-active');
	$('.card-tabs .tab').removeClass('is-active');
	$('.card-tabs .tab:eq('+btnNum+')').addClass('is-active');
});



//   

$('.top-slider').owlCarousel({
		animateOut: 'fadeOut',
		animateIn: 'fadeIn',
		mouseDrag: false,
		touchDrag:false,
		loop:true,
		nav: true,
		dots: true,
		items: 1,
		navText: [,],
		autoplay: true,
		autoplayHoverPause: true,
		autoplayTimeout:8000,
		autoplaySpeed: 5000
	});

$('.hit-slider').owlCarousel({
		mouseDrag: true,
		touchDrag:true,
		margin: 5,
		loop:true,
		nav: true,
		dots: false,
		items: 4,
		navText: [,],
		autoplay: true,
		autoplayHoverPause: true,
		autoplayTimeout:10000,
		autoplaySpeed: 10000,
		responsive: {
			0:{
				items:1
			},
			480:{
				items:2
			},
			767:{
				items:3
			},
			1024:{
				items:4
			}
		}
	});

$('.home__collection').owlCarousel({
		mouseDrag: true,
		touchDrag:true,
		margin: 0,
		loop:false,
		nav: false,
		dots: true,
		items: 1,
		navText: [,],
		dotsData: true,
		autoplay: false,
		autoplayHoverPause: true,
		autoplayTimeout:10000,
		autoplaySpeed: 10000
	});

$('.home__collection').prepend($('.home__collection .owl-dots')); //   

$('.home__review').owlCarousel({
		mouseDrag: true,
		touchDrag:true,
		margin: 0,
		loop:true,
		nav: true,
		dots: false,
		items: 1,
		navText: [,],
		autoplay: true,
		autoplayHoverPause: true,
		autoplayTimeout:10000,
		autoplaySpeed: 1000
	});

